import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';

import { API_CONFIG } from 'src/config/api.config';
import { CreateEnderecoDTO } from '../../interfaces/enderecos/CreateEnderecoDTO';
import { CreatedLotericaResponseDTO } from '../../interfaces/lotericas/CreatedLotericaResponseDTO';
import { CreateLotericaDTO } from '../../interfaces/lotericas/CreateLotericaDTO';
import { LotericaDTO } from '../../interfaces/lotericas/LotericaDTO';

@Injectable({
  providedIn: 'root'
})
export class LotericaService {

  constructor(
    private http: HttpClient,
  ) { }

  list(): Observable<LotericaDTO[]> {
    return this.http.get<LotericaDTO[]>(`${API_CONFIG.baseURL}/loterica`, {
      responseType: 'json',
    });
  }

  createLoterica(newLoterica: CreateLotericaDTO): Observable<CreatedLotericaResponseDTO> {
    return this.http.post<CreatedLotericaResponseDTO>(
      `${API_CONFIG.baseURL}/loterica`,
      newLoterica,
      {
        responseType: 'json',
      });
  }


  updateLoterica(updatedLoterica: CreateLotericaDTO, loterica_id: string): Observable<CreatedLotericaResponseDTO> {
    return this.http.put<CreatedLotericaResponseDTO>(
      `${API_CONFIG.baseURL}/loterica/${loterica_id}`,
      updatedLoterica,
      {
        responseType: 'json',
      });
  }

  createEndereco(newEndereco: CreateEnderecoDTO, user_id: string): Observable<any> {
    return this.http.post<any>(
      `${API_CONFIG.baseURL}/loterica/${user_id}/endereco`,
      newEndereco,
      {
        responseType: 'json',
      });
  }

  updateEndereco(newEndereco: CreateEnderecoDTO, user_id: string): Observable<any> {
    return this.http.put<any>(
      `${API_CONFIG.baseURL}/loterica/${user_id}/endereco`,
      newEndereco,
      {
        responseType: 'json',
      });
  }
}
